<template>
     <div class="my-popups modal right fade show" style="display:block" role="dialog">
        <div class="modal d-block " tabindex="-1" role="dialog">
            <div class="modal-dialog doc-setup-slide">
                <div class="modal-content">
                    <div class="modal-header mheader-user py-0">
                    <button type="button" class="close" @click="close()">
                        <i aria-hidden="true" class="fa fa-times"></i>
                    </button>
                    <h4 class="modal-title py-3">{{ $t('Configuration') }}</h4>
                </div>
                  <div class="modal-body bg-white">
                        <section class="container-fluid p-0">
                            <div class="left-menu-tab new_tab">
                                
                                <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);">
                                    
                                    <!-- <ul v-if="popupCondition=='Configuration'" class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">
                                        <li class="nav-item FourthScreen" @click="SkipTab(1)"><a
                                                class="nav-link left-menu" :class="{'active':step==1}"  style="cursor: pointer;"><i class="fa fa-gear pr-2 text-muted"
                                                    aria-hidden="true"></i><span class="font-14">{{$t('General')}}</span></a></li>

                                                    <li  class="nav-item SecondScreen" @click="SkipTab(2)"><a
                                                class="nav-link left-menu" :class="{'active':step==2}"  style="cursor: pointer;"><i class="fa fa-clock-o pr-2 text-muted"
                                                     aria-hidden="true"></i><span class="font-14">{{$t('TimeTracker')}}</span></a>
                                        </li>
                                        <li  class="nav-item ThirdScreen" @click="SkipTab(3)"><a
                                                class="nav-link left-menu" :class="{'active':step==3}"  style="cursor: pointer;"><i class="fa fa-check pr-2 text-muted"
                                                     aria-hidden="true"></i><span class="font-14">{{$t('Approval')}}</span></a>
                                        </li>
                                      
                                    </ul> -->

                                    <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="upper" role="tablist">
            <li class="nav-item" v-for="item in moduleddata" :key="item">
                <a id="leftmenu" class="nav-link left-menu " @click="activateTab(item)" :class="{ 'active': selectedModule === item }">
                    <i id="itemdata" v-bind:class="item.classicon"></i>&nbsp;
                    {{ item.moduleTitle }}
                </a>
            </li>
        </ul>
                                </div>
                            </div>
                            <div class="right-content">
                                
                                   <div class="col-md-12 p-0" v-if="popupCondition=='Configuration'">
                                     <Configuration :CurrentTab="CurrentTab" v-if="popupCondition == 'Configuration'" :moduleKeys="moduleKeys" :modulename="modulename"></Configuration>
                                       </div>
                            </div>

                        </section>
                  </div>
                </div>
            </div>
        </div>
     </div>
</template>
<script>
import Configuration from "../../views/AdvanceSetup/Configuration.vue"
import DataService from '../../services/DataService';
export default {
    components:{
        Configuration
    },
    data() {

        
        return {
            step:1,
            click:'SetupAsset',
            CurrentTab:1,
            IsClientStoragesCopmleted:false,


            activeKey: 0,
            moduleddata: [],
            moduleddataAll: [],
            searchQuery: "",
            modulename: "",
            moduletitle: [],
            classicon: [],
            activeKey: null,
            moduleKeys: [],
            validate: [],
            saveArray: [],
            showsavebutton: false,
            classadded: '',
            isLoading: false,
            dynamicclass: '',
            selectedModule: null,
            IsSearchItem: false,
            id: null,
            attachmentDiv: '',
            blobUrl: '',
            imgPath: '',
            UserType: '',
            CompanyLogoCoreConfigId: 0,
            timeZoneData: [],
            getTimeZone: '',
            timeZoneKeys: 0,
            Month: '',
            year: '',
            Number: '',
            arraydata: [],
            defaultdata: [],
            opennumbering: false,
            CheckBoxClass: '',
            salesorderdata: [],
            shiftData: [],
            designData: [],
            departData: [],
            locationData: [],
            currencyData:[],
        }
    },
    created(){
        var vm = this;

        vm.GetLeftModulesdata();
    },
    mounted: function(){
        var vm= this;
        $('#modalManageView').on('blur', function(){
             this.IsVisible=false;
        });
    },
    props:{
       IsVisible:{
            type: Boolean,
            required: true
        },
        popupCondition:{
            type: String,
            required: false
        }
    },
    watch: {
        IsVisible: {
            handler(val, older) {
                if (val) {
                    this.IsVisible=val
                }

            }
        }
    },
    methods:{
         close: function () {
            this.$parent.IsshowConfiguration=false;
        },
        SkipTab:function(s){
            debugger
            var vm=this;
            vm.step=s;
            vm.CurrentTab=s;
        },
        BackToStep: function(s){
            var vm=this;
            vm.step=s;  
        },
        async GetLeftModulesdata() {
            var vm = this;
            await DataService.GetAllModule().then((response) => {
                vm.moduleddata = response.data;
                vm.moduleddataAll = vm.moduleddata;
                vm.moduleddata.forEach((item) => {
                    switch (item.moduleTitle) {
                        case "Approval":
                            item.classicon = "fa fa-thumbs-up action-icon";
                            break;
                        case "Client Bill":
                            item.classicon = "fa fa-sticky-note action-icon";
                            break;
                        case "Clients":
                            item.classicon = "fa fa-users action-icon";
                            break;
                        case "Cloud Storage":
                            item.classicon = "fa fa-database action-icon";
                            break;
                        case "CRM":
                            item.classicon = "fa fa-tasks action-icon";
                            break;
                        case "Emails":
                            item.classicon = "fa fa-envelope action-icon";
                            break;
                        case "Expense Approval":
                            item.classicon = "fa fa-check action-icon";
                            break;
                        case "Expenses":
                            item.classicon = "fa fa-money action-icon";
                            break;
                        case "General":
                            item.classicon = "fa fa-cog action-icon";
                            break;
                        case "Hiring":
                            item.classicon = "fa fa-list-alt action-icon";
                            break;
                        case "Human Resource":
                            item.classicon = "fa fa-user action-icon";
                            break;
                        case "Knowledge Base":
                            item.classicon = "fa fa-book action-icon";
                            break;
                        case "Microsoft Exchange":
                            item.classicon = "fa fa-windows action-icon";
                            break;
                        case "Product Management":
                            item.classicon = "fa fa-building action-icon";
                            break;
                        case "Project":
                            item.classicon = "fa fa-thumbs-up action-icon";
                            break;
                        case "Project files":
                            item.classicon = "fa fa-folder-open action-icon";
                            break;
                        case "Project Message Board":
                            item.classicon = "fa fa-comment action-icon";
                            break;
                        case "Quote":
                            item.classicon = "fa fa-file-text action-icon";
                            break;
                        case "Storage":
                            item.classicon = "fa fa-database action-icon";
                            break;
                        case "Ticketing":
                            item.classicon = "fa fa-street-view action-icon";
                            break;
                        case "Time Tracker":
                            item.classicon = "fa fa-clock-o action-icon";
                            break;
                        case "Timesheet":
                            item.classicon = "fa fa-calendar action-icon";
                            break;
                        case "Training":
                            item.classicon = "fa fa-pencil-square action-icon";
                            break;
                        case "Work Diary":
                            item.classicon = "fa fa-picture-o action-icon";
                            break;
                        default:
                            item.classicon = "fa fa-pencil";
                    }
                });
                if (vm.moduleddata.length > 0) {
                    setTimeout(function () {
                        vm.activateTab(vm.moduleddata[0]);
                    }, 100);
                }
            });
        },
        async activateTab(item) {
            var vm = this;
            vm.isLoading = true;
            if (item != undefined) {
                vm.dynamicclass = item.classicon
                vm.showsavebutton = true;
                vm.modulename = item.moduleTitle;
                vm.activeKey = item.moduleId;
                vm.selectedModule = item;
            }
            var params = `moduleId=${vm.activeKey}&IsWizard=`;
            await DataService.GetKeys(params).then((response) => {
                if (response.data.keyList.length > 0) {
                    vm.moduleKeys = response.data.keyList

                    if (vm.moduleKeys.length > 5 && vm.moduleKeys[5]) {
                        vm.imgPath = vm.moduleKeys[5].coreConfigValue || '';
                        vm.CompanyLogoCoreConfigId = vm.moduleKeys[5].coreConfigId || '';
                    } else {

                        vm.imgPath = '';
                        vm.CompanyLogoCoreConfigId = '';
                    }
                    // vm.GetBlobUrl();
                    // vm.GetTimeZone();
                    // vm.GetDefaultTempData();
                    // vm.GetSalesOrder();
                    // vm.GetShiftData();
                    // vm.GetDesignationData();
                    // vm.GetLocation();
                    // vm.GetDepartmentData();
                    // vm.GetCurrencyData();
                }
                vm.isLoading = false;

            })

        },
    }
}
</script>